import React, { memo, useContext } from 'react';
import { ProjectsPhotoCarousel } from './ProjectsPhotoCarousel';
import { Col, Row } from 'antd';
import { ProjectsCollapse } from './ProjectsCollapse';
import { useEffectOnce, useTitle } from 'react-use';
import { NavigationContext } from '../../main-layouts/components/NavigationContext';

export const ProjectsPage = memo(() => {
  const { openedMenu, setOpenedMenu } = useContext(NavigationContext);

  useTitle('Проекты');

  useEffectOnce(() => {
    setOpenedMenu?.('projects');
  });

  return (
    <Row justify={'center'}>
      <Col>
        <ProjectsPhotoCarousel />

        <br />

        <ProjectsCollapse />
      </Col>
    </Row>
  );
});
